* {
  margin: 0;
  padding: 0;
}

nav {
  height: 50px;
  background-color: skyblue;
  box-shadow: 0px 2px 8px gray;
}
li {
  display: inline-block;
  list-style: none;
  margin: 10px;
  text-decoration: none;
  cursor: pointer;
}
.active {
  text-decoration: none;
  font-weight: bold;
}
